        this.preV = { x: null, y: null };    // 
        this.x1 = this.x2 = this.y1 = this.y2 = null;    // start()x1, y1, move()x2, y2 ()
        this.pinchStartLen = null;    // start()
        this.scale = 1;    // 1
        this.isDoubleTap = false;    // , false

        this.tapTimeout = null;    // tap
        this.singleTapTimeout = null;    // singleTap
        this.longTapTimeout = null;    // longTap()
        this.swipeTimeout = null;    // swipe

